import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Duyurular, DuyurularJoin } from 'src/app/models/genel/duyurular';
import { tap } from 'rxjs/operators';
import { SonucBildirimi } from 'src/app/models/sonuc-bildirimi';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DuyurularService {

  public loading: boolean = false;
  public buttonDisabled = true;
  public selected = [];
  public islemTuru: BehaviorSubject<number>;
  public size = 20;
  public SayfayiYenile: BehaviorSubject<boolean>;
  public toastposition = 'bottom-right';

  private controller: string = 'api/Duyurular';
  private apiUrl: string = `${environment.baseurl}${this.controller}/`;

  constructor(private httpclient: HttpClient) {
    this.islemTuru = new BehaviorSubject<number>(1);
    this.SayfayiYenile = new BehaviorSubject<boolean>(false);
  }

  public getIslemTuru(): Observable<number> {
    return this.islemTuru.asObservable();
  }

  public getSayfayiYenile(): Observable<boolean> {
    return this.SayfayiYenile.asObservable();
  }

  public AddDuyurular(data: Duyurular) {
    return this.httpclient.post<SonucBildirimi>(this.apiUrl + 'AddDuyurular', data).pipe(tap(x => {
      this.loading = true;
    }));
  }

  public UpdateDuyurular(data: Duyurular) {
    return this.httpclient.put<SonucBildirimi>(this.apiUrl + 'UpdateDuyurular', data).pipe(tap(x => {
      this.loading = true;
    }));
  }

  public DeleteDuyurular(id: number) {
    return this.httpclient.delete<SonucBildirimi>(this.apiUrl + `DeleteDuyurular/${id}`).pipe(tap(x => {
      this.selected = [];
      this.buttonDisabled = true;
    }));
  }

  public GetDuyurularByID(id: number) {
    return this.httpclient.get<Duyurular[]>(this.apiUrl + `GetDuyurularByID/${id}`);
  }

  public GetDuyurularAll() {
    return this.httpclient.get<Duyurular[]>(this.apiUrl + 'GetDuyurularAll');
  }
  public GetDuyurularAllJoin() {
    return this.httpclient.get<DuyurularJoin[]>(this.apiUrl + 'GetDuyurularAllJoin');
  }

  public GetDuyurularAllJoinDashbord(){
    return this.httpclient.get<DuyurularJoin[]>(this.apiUrl + 'GetDuyurularAllJoinDashbord');
  }

}
